const CACHE_PREFIX = 'weather_'
const MAX_CACHE_ITEMS = 10

export default {
  // Ëé∑ÂèñÁºìÂ≠ò
  get(cityId) {
    const cacheKey = `${CACHE_PREFIX}${cityId}`
    const cachedData = localStorage.getItem(cacheKey)

    if (!cachedData) return null

    try {
      const parsed = JSON.parse(cachedData)

      // Ê£ÄÊü•ÊòØÂê¶ËøáÊúü
      if (Date.now() > parsed.timestamp + parsed.expires * 1000) {
        this.remove(cityId)
        return null
      }

      return parsed.data
    } catch (e) {
      this.remove(cityId)
      return null
    }
  },

  // ËÆæÁΩÆÁºìÂ≠ò
  set(cityId, data, isForecast = false) {
    const cacheKey = `${CACHE_PREFIX}${cityId}`
    const cacheData = {
      data,
      timestamp: Date.now(),
      expires: isForecast ? 3600 : 600 // È¢ÑÊä•ÁºìÂ≠ò1Â∞èÊó∂ÔºåÂΩìÂâçÂ§©Ê∞î10ÂàÜÈíü
    }

    localStorage.setItem(cacheKey, JSON.stringify(cacheData))
    this.cleanup() // Ê∏ÖÁêÜÊóßÁºìÂ≠ò
  },

  // Âà†Èô§ÁºìÂ≠ò
  remove(cityId) {
    localStorage.removeItem(`${CACHE_PREFIX}${cityId}`)
  },

  // Ê∏ÖÁêÜËøáÊúüÂíåÂ§ö‰ΩôÁöÑÁºìÂ≠ò
  cleanup() {
    const keys = Object.keys(localStorage).filter(k =>
      k.startsWith(CACHE_PREFIX)
    )

    // Ê∏ÖÁêÜËøáÊúüÁºìÂ≠ò
    keys.forEach(key => {
      const cached = localStorage.getItem(key)
      try {
        const parsed = JSON.parse(cached)
        if (Date.now() > parsed.timestamp + parsed.expires * 1000) {
          localStorage.removeItem(key)
        }
      } catch (e) {
        localStorage.removeItem(key)
      }
    })

    // Â¶ÇÊûú‰ªçÁÑ∂Ë∂ÖËøáÊúÄÂ§ßÊï∞ÈáèÔºåÊ∏ÖÁêÜÊúÄÊóßÁöÑ
    const remainingKeys = Object.keys(localStorage).filter(k =>
      k.startsWith(CACHE_PREFIX)
    )
    if (remainingKeys.length > MAX_CACHE_ITEMS) {
      remainingKeys
        .map(key => ({
          key,
          timestamp: JSON.parse(localStorage.getItem(key)).timestamp
        }))
        .sort((a, b) => a.timestamp - b.timestamp)
        .slice(0, remainingKeys.length - MAX_CACHE_ITEMS)
        .forEach(item => localStorage.removeItem(item.key))
    }
  },

  // Ê∏ÖÁ©∫ÊâÄÊúâÂ§©Ê∞îÁºìÂ≠ò
  clearAll() {
    Object.keys(localStorage)
      .filter(k => k.startsWith(CACHE_PREFIX))
      .forEach(key => localStorage.removeItem(key))
  }
}
