import axios from 'axios'

// ÂàõÂª∫Ëá™ÂÆö‰πâ Axios ÂÆû‰æã
const weatherApi = axios.create({
  baseURL: 'https://api.openweathermap.org/data/2.5',
  timeout: 10000, // 10ÁßíË∂ÖÊó∂
  params: {
    units: 'metric',
    lang: 'ja',
    appid: process.env.VUE_APP_OPENWEATHER_API_KEY
  }
})

// ÈáçËØïÈÖçÁΩÆ
const retryConfig = {
  retries: 3, // ÊúÄÂ§ßÈáçËØïÊ¨°Êï∞
  retryDelay: 1000, // ÈáçËØïÂª∂Ëøü(ms)
  retryOn: [500, 502, 503, 504, 429] // ÈúÄË¶ÅÈáçËØïÁöÑÁä∂ÊÄÅÁ†Å
}

// ËØ∑Ê±ÇÊã¶Êà™Âô®
weatherApi.interceptors.request.use(
  config => {
    // ÂèØ‰ª•Âú®ËøôÈáåÊ∑ªÂä†ËØ∑Ê±ÇÂâçÁöÑÈÄªËæë
    return config
  },
  error => {
    return Promise.reject(error)
  }
)

// ÂìçÂ∫îÊã¶Êà™Âô®ÔºàÂÆûÁé∞ÈáçËØïÈÄªËæëÔºâ
weatherApi.interceptors.response.use(
  response => response,
  async error => {
    const { config, response } = error

    // Â¶ÇÊûúÊ≤°ÊúâÈÖçÁΩÆÈáçËØïÊàñËÄÖËØ∑Ê±ÇÊñπÊ≥ï‰∏çÊòØGETÔºàÈÄöÂ∏∏Âè™ÈáçËØïGETËØ∑Ê±ÇÔºâ
    if (!config || !retryConfig.retries || config.method !== 'get') {
      return Promise.reject(error)
    }

    // Ê£ÄÊü•ÊòØÂê¶ÈúÄË¶ÅÈáçËØï
    const shouldRetry = retryConfig.retryOn.includes(response?.status)
    if (!shouldRetry) {
      return Promise.reject(error)
    }

    // ËÆæÁΩÆÈáçËØïÊ¨°Êï∞
    config.__retryCount = config.__retryCount || 0

    // Ê£ÄÊü•ÊòØÂê¶ËææÂà∞ÊúÄÂ§ßÈáçËØïÊ¨°Êï∞
    if (config.__retryCount >= retryConfig.retries) {
      return Promise.reject(error)
    }

    // Â¢ûÂä†ÈáçËØïËÆ°Êï∞Âô®
    config.__retryCount += 1

    // ÂàõÂª∫Êñ∞ÁöÑ Promise Êù•Â§ÑÁêÜÊåáÊï∞ÈÄÄÈÅø
    const delay = new Promise(resolve => {
      setTimeout(() => {
        resolve()
      }, retryConfig.retryDelay * config.__retryCount) // ÊåáÊï∞ÈÄÄÈÅø
    })

    // Âú®Âª∂ËøüÂêéÈáçËØïËØ∑Ê±Ç
    return delay.then(() => weatherApi(config))
  }
)

export default weatherApi
